





















export default {
	data() {
		return {
			addMoneyNum: [
				{
					id: 5,
					bgColor: true
				},
				{
					id: 10,
					bgColor: false
				},
				{
					id: 20,
					bgColor: false
				},
				{
					id: 50,
					bgColor: false
				},
				{
					id: 100,
					bgColor: false
				}
			],
			inputMoeny: false
		};
	},
	methods: {
		moneyNum(index) {
			//选择充值金额
			this.addMoneyNum.forEach((v, i) => {
				// console.log(v,i)
				v.bgColor = false;
			});
			this.addMoneyNum[index].bgColor = true;
			this.inputMoeny = false;
		},
		inputMoenyNum() {
			// 输入 充值金额
			this.addMoneyNum.forEach((v, i) => {
				// console.log(v,i)
				v.bgColor = false;
			});
			this.inputMoeny = true;
		}
	}
};
